
"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { useToast } from "@/hooks/use-toast";
import { Github, Linkedin, Mail, Send, User, MessageSquare } from "lucide-react";
import { useState, type FormEvent } from "react";

export default function ContactSection() {
  const { toast } = useToast();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsSubmitting(true);

    // Simulação de envio de formulário
    await new Promise(resolve => setTimeout(resolve, 1500));

    console.log({ name, email, message }); // Em um app real, você enviaria isso para um backend

    toast({
      title: "Mensagem Enviada!",
      description: "Obrigado por entrar em contato. Responderei em breve.",
      variant: "default",
    });

    setName("");
    setEmail("");
    setMessage("");
    setIsSubmitting(false);
  };

  return (
    <section id="contact" className="py-16">
      <div className="text-center mb-12">
        <h2 className="text-3xl md:text-4xl font-bold text-primary">Entre em Contato</h2>
        <p className="text-md text-muted-foreground mt-2">Vamos conversar sobre seu próximo projeto ou ideia!</p>
      </div>
      <Card className="max-w-3xl mx-auto shadow-xl bg-card/80 backdrop-blur-sm">
        <CardHeader>
          <CardTitle className="text-2xl text-primary">Envie uma Mensagem</CardTitle>
          <CardDescription>Preencha o formulário abaixo ou me encontre nas redes sociais.</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label htmlFor="name" className="text-foreground/80 flex items-center">
                  <User className="mr-2 h-4 w-4 text-primary" /> Nome
                </Label>
                <Input 
                  id="name" 
                  placeholder="Seu nome completo" 
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  required 
                  className="bg-input/50 focus:bg-input/70 pl-3"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="email" className="text-foreground/80 flex items-center">
                  <Mail className="mr-2 h-4 w-4 text-primary" /> Email
                </Label>
                <Input 
                  id="email" 
                  type="email" 
                  placeholder="seu@email.com" 
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required 
                  className="bg-input/50 focus:bg-input/70 pl-3"
                />
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="message" className="text-foreground/80 flex items-center">
                <MessageSquare className="mr-2 h-4 w-4 text-primary" /> Mensagem
              </Label>
              <Textarea 
                id="message" 
                placeholder="Sua mensagem..." 
                rows={5}
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                required 
                className="bg-input/50 focus:bg-input/70 pl-3"
              />
            </div>
            <div className="flex justify-end">
              <Button type="submit" className="bg-primary hover:bg-primary/90 text-primary-foreground" disabled={isSubmitting}>
                {isSubmitting ? "Enviando..." : <>Enviar Mensagem <Send className="ml-2 h-4 w-4" /></>}
              </Button>
            </div>
          </form>
          <div className="mt-10 pt-6 border-t border-border">
            <h3 className="text-lg font-semibold text-center text-foreground/90 mb-4">Ou me encontre em:</h3>
            <div className="flex justify-center space-x-6">
              <a href="mailto:danielmartinsjob@gmail.com" target="_blank" rel="noopener noreferrer" className="text-muted-foreground hover:text-primary transition-colors">
                <Mail className="h-8 w-8" />
                <span className="sr-only">Email</span>
              </a>
              <a href="https://linkedin.com/in/danielestevao" target="_blank" rel="noopener noreferrer" className="text-muted-foreground hover:text-primary transition-colors">
                <Linkedin className="h-8 w-8" />
                <span className="sr-only">LinkedIn</span>
              </a>
              <a href="https://github.com/dvizioon" target="_blank" rel="noopener noreferrer" className="text-muted-foreground hover:text-primary transition-colors">
                <Github className="h-8 w-8" />
                <span className="sr-only">GitHub</span>
              </a>
            </div>
          </div>
        </CardContent>
      </Card>
    </section>
  );
}
